.portfolio {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: yellowgreen;
}

.name {
  font-size: 24px;
  margin-bottom: 10px;
  color: rgb(38, 39, 151);
}

.section-heading {
  font-size: 20px;
  margin-bottom: 10px;
  color: red;
}

.education-list,
.skills-list,
.certifications-list,
.contact-list {
  list-style-type: none;
  padding: 0;
}

.education-item,
.skill-item,
.certification-item,
.contact-item {
  margin-bottom: 10px;
}

.resume-link {
  display: inline-block;
  padding: 10px 20px;
  background-color: #333;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
}

.resume-link:hover {
  background-color: #555;
}

.contact-list li a {
  color: #333;
  text-decoration: none;
}

.contact-list li a:hover {
  color: #555;
}
